html {
  font-size: 16px;
}

body {
  margin: 0;
  font-family: "UbuntuCondensed-Regular";
  src: url("./fonts/UbuntuCondensed-Regular.ttf") format("tff"), url("./fonts/UbuntuCondensed-Regular.ttf") format("ttf");
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: floralwhite;
}

h1, h2, h3, h4, h5, h6, p {font-family: "UbuntuCondensed-Regular";}
main h1, main h2, main h3, main h4, main h5, main h6, main p {color: #330000;}

nav {
  border-bottom: 2px solid black;
  width: 100%;
  height: 65px;
}

nav, footer {
  background-color: hsl(16, 70%, 50%);
}

nav h4 {letter-spacing: 4px;}

a:link {
  color: orange;
  font-size: 14px;
}

a:hover {
  color:  rgb(230, 149, 0);
  font-size: 14px;
}

a:visited {
  color: orange;
  font-size: 14px;
}

a:active {
  color: rgb(255, 204, 102);
  font-size: 14px;
}

main {padding: 24px}

.article article {
  background-color:  #ffffe6;
  border-top: 1px solid orange;
  border-bottom: 1px solid orange;
  padding: 8px;
}

.fa-arrow-up {
  color: green;
}

.fa-arrow-down {
  color: red;
}

.carousel {
  width: 100%;
  height: auto;
  margin: auto;
}

.carousel .arrow {
  display: flex; 
  flex-direction: column; 
  justify-content: center; 
  align-content: center;
}

.carousel .arrow_span {
  cursor: pointer;
}

.carousel .img {
  display: flex; 
  flex-direction: column; 
  justify-content: center; 
  align-content: center;
  padding: 12px;
  border: 1px solid black;
  -moz-box-shadow:    1px 1px 2px 2px saddlebrown;
  -webkit-box-shadow: 1px 1px 2px 2px saddlebrown;
  box-shadow:         1px 1px 2px 2px saddlebrown;
}

/* media queries */

/* smartphones, iPhone, portrait 480x320 phones */
@media (min-width:320px) {
  html {font-size: 60%}
}

/* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */
@media (min-width:481px) {
  html {font-size: 60%}
}

/* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */
@media (min-width:641px) {
  html {font-size: 75%}
}

/* tablet, landscape iPad, lo-res laptops ands desktops */
@media (min-width:961px) {
  html {font-size: 100%}
}

/* big landscape tablets, laptops, and desktops */
@media (min-width:1025px) {
  html {font-size: 100%}
}

